import React, { Component, lazy } from "react";
import { Link } from "react-router-dom";
import {
  formatDate,
  localStoragedecryp,
  localStorageencryp,
} from "../../../../components/helpers/function";
import { ToastNotification } from "../../../../components/helpers/notification";
import {
  Panel,
  PanelBody,
  PanelHeader,
} from "../../../../components/panel/panel";
import { getDataNoTimeOut } from "../../../../config/axios";
import * as moment from "moment-timezone";
const HeadLaporanBayar = lazy(() => import("./HeadLaporanBayar"));

class FormLaporanPenjualanBayar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      LaporanPenjualanBayar: [],
      export: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }
  componentDidMount() {
    let data = JSON.parse(localStoragedecryp("userdata"));
    this.setState({ username: data.user_id });
  }
  handleSubmit(data) {
    this.setState({ isLoading: true });
    let data_post = {
      tgl_awal: formatDate(
        moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")
      ),
      tgl_akhir: formatDate(
        moment(new Date(data.tgl_akhir)).format("YYYY-MM-DD")
      ),
    };
    getDataNoTimeOut("penjualan/get/reportbayar", data_post)
      .then((res) => {
        if (res.data.length === 0) {
          ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
          this.setState({
            export: false,
            isLoading: false,
          });
        } else {
          ToastNotification("success", "Data Laporan Transaksi Penjualan Tersedia");
          localStorageencryp("tgl_laporan", JSON.stringify(data_post));
          this.setState({
            export: true,
            isLoading: false,
            LaporanPenjualanBayar: res.data,
          });
        }
      })
      .catch((error) => {
        this.setState({
          export: false,
          isLoading: false,
        });
        ToastNotification("info", "Data Yang Di Cari Tidak Ada !!!");
      });
  }

  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Penjualan Bayar</li>
        </ol>
        <h1 className="page-header">Laporan Penjualan Bayar </h1>
        <Panel>
          <PanelHeader>Laporan Penjualan Bayar</PanelHeader>
          <PanelBody>
            {/* {console.log(this.state.hidden)} */}
            <HeadLaporanBayar
              export={this.state.export}
              isLoading={this.state.isLoading}
              LaporanPenjualanBayar={this.state.LaporanPenjualanBayar}
              onSubmit={(data) => this.handleSubmit(data)}
            />
          </PanelBody>
        </Panel>
      </div>
    );
  }
}
export default FormLaporanPenjualanBayar;
